import React from "react";
import {
  Text,
  View,
  ImageBackground,
  ScrollView,
  SafeAreaView,
} from "react-native";
import { useNavigation } from "@react-navigation/native";
import ShortButton from "../components/buttons/ShortButton";
import { styles } from "../styles/Styles";
import { MaterialCommunityIcons } from "@expo/vector-icons";

const ageGroup = [
  {
    name: "Children",
    icon: <MaterialCommunityIcons name="human-child" size={50} color="black" />,
  },
  {
    name: "Youth",
    icon: (
      <MaterialCommunityIcons name="human-handsdown" size={50} color="black" />
    ),
  },
  {
    name: "Adult",
    icon: <MaterialCommunityIcons name="human" size={50} color="black" />,
  },
];

export default function Questionaire1() {
  const navigation = useNavigation();
  const handlePress = (selectedAge) => {
    navigation.navigate("Questionaire2", {
      selectedAge: selectedAge,
    });
  };

  return (
    <SafeAreaView style={{ flex: 1 }}>
      <ImageBackground
        source={require("../assets/sdgsImage.png")}
        style={styles.imageBackground}
        imageStyle={styles.imageStyle}
      >
        <ScrollView style={styles.scrollingViewMainContainer}>
          <View style={styles.viewContainer}>
            <View style={styles.titleViewContainer}>
              <Text style={styles.font20px600}>Which is your age group?</Text>
            </View>
            <View style={styles.shortButtonsViewContainer}>
              {ageGroup.map((group) => (
                <View key={group.name} style={styles.shortButtonContainer}>
                  <ShortButton
                    onPress={() => handlePress(group.name)}
                    textTitle={group.name}
                    icon={group.icon}
                  />
                </View>
              ))}
            </View>
          </View>
        </ScrollView>
      </ImageBackground>
    </SafeAreaView>
  );
}
