import React from "react";
import { ScrollView, View, Text, Image, Linking } from "react-native";
import { FontAwesome } from "@expo/vector-icons";
import { Entypo } from "@expo/vector-icons";
import { SimpleLineIcons } from "@expo/vector-icons";
import Footer from "../components/globals/Footer";
import { LinearGradient } from "expo-linear-gradient";
import { makeCall } from "../utils/makeCall";

export default function OrganizationDetails({ route }) {
  const item = route.params;

  return (
    <ScrollView>
      <View>
        <View>
          <Image
            source={require("../assets/logos/no_logo.png")}
            resizeMode="contain"
          />
        </View>
      </View>
      <View>
        <Text>{item["Name of Organisation"]}</Text>
        {!item["Phone Number"] ? (
          <></>
        ) : (
          <View>
            <View>
              <Entypo
                name="old-mobile"
                size={24}
                color="#6B6B6B"
                style={{ marginRight: 2, marginLeft: -4 }}
              />
              <View>
                <Text>Phone</Text>
              </View>
            </View>
            {item["Phone Number"]
              .split("/")
              .map((num) => num.trim())
              .map((num) => (
                <Text key={"phone" + num} onPress={() => makeCall(num)}>
                  {` ${num}`}
                </Text>
              ))}
          </View>
        )}
        <View>
          <View>
            <FontAwesome
              name="building"
              size={20}
              color="#6B6B6B"
              style={{ marginRight: 4 }}
            />
            <View>
              <Text>Address</Text>
            </View>
          </View>
          {!item["Address"] ? <></> : <Text>{item["Address"]}</Text>}
          <Text>{item["Code_region"]}</Text>
        </View>
        <View>
          {!item["Email"] ? (
            <></>
          ) : (
            <View>
              <View>
                <FontAwesome
                  name="envelope"
                  size={20}
                  color="#6B6B6B"
                  style={{ marginRight: 6 }}
                />
                <View>
                  <Text>Email</Text>
                </View>
              </View>
              <View>
                <Text>{item["Email"]}</Text>
              </View>
            </View>
          )}
        </View>
        <View>
          {!item["Web Address"] ? (
            <></>
          ) : (
            <View>
              <View>
                <SimpleLineIcons
                  name="globe"
                  size={20}
                  color="#6B6B6B"
                  style={{ marginRight: 6 }}
                />
                <View>
                  <Text>Website</Text>
                </View>
              </View>
              <Text
                onPress={() =>
                  Linking.openURL(item["Web Address"]).catch((err) =>
                    console.error("Couldn't load page", err)
                  )
                }
              >
                {item?.["Web Address"]}
              </Text>
            </View>
          )}
        </View>
      </View>
      <View style={{ backgroundColor: "rgba(255, 255, 255, 1)" }}>
        <LinearGradient
          colors={["rgb(239,236,232)", "rgb(255,255,255)"]}
          style={{ padding: 20 }}
        >
          <View>
            <Text>Areas of support</Text>
            <Text>- {item["Needs"].split(", ").join("\n- ")}</Text>
          </View>
          <View>
            <Text>Target group</Text>
            <Text>- {item["Target group"].split("\n").join("\n- ")}</Text>
          </View>
        </LinearGradient>
        <View>
          <Text>Objective</Text>
          <Text>{item["Objective"]}</Text>
        </View>
      </View>
      <Footer />
    </ScrollView>
  );
}
