import React from "react";
import {
  Text,
  View,
  ImageBackground,
  ScrollView,
  SafeAreaView,
} from "react-native";
import { useNavigation } from "@react-navigation/native";
import LongButton from "../components/buttons/LongButton";
import { styles } from "../styles/Styles";

const needs = [
  {
    name: "Food and water",
    icon: null,
  },
  {
    name: "Shelter",
    icon: null,
  },
  {
    name: "Financial help / income generating",
    icon: null,
  },
  {
    name: "Health",
    icon: null,
  },
  {
    name: "Mental health",
    icon: null,
  },
  {
    name: "Education",
    icon: null,
  },
  {
    name: "Employment",
    icon: null,
  },
  {
    name: "Human rights (victims of violence, abusement)",
    icon: null,
  },
];

export default function Questionaire3({ route }) {
  const navigation = useNavigation();

  const handlePress = (selectedNeed) => {

    navigation.navigate("Results", {
      selectedAge: route.params["selectedAge"],
      selectedGender: route.params["selectedGender"],
      selectedNeed: selectedNeed,
    });
  };
  return (
    <SafeAreaView style={{ flex: 1 }}>
      <ImageBackground
        source={require("../assets/sdgsImage.png")}
        style={styles.imageBackground}
        imageStyle={styles.imageStyle}
      >
        <ScrollView style={styles.scrollingViewMainContainer}>
          <View style={styles.viewContainer}>
            <View style={styles.titleViewContainer}>
              <Text style={styles.font20px600}>
                What do you need help with?
              </Text>
            </View>
            <View style={styles.longButtonsViewContainer}>
              {needs.map((group) => (
                <View key={group.name} style={styles.longButtonContainer}>
                  <LongButton
                    onPress={() => handlePress(group.name)}
                    textTitle={group.name}
                    icon={group.icon}
                  />
                </View>
              ))}
            </View>
          </View>
        </ScrollView>
      </ImageBackground>
    </SafeAreaView>
  );
}
