import React from "react";
import {
  Text,
  View,
  ImageBackground,
  ScrollView,
  SafeAreaView,
} from "react-native";
import { useNavigation } from "@react-navigation/native";
import ShortButton from "../components/buttons/ShortButton";
import { styles } from "../styles/Styles";
import { MaterialCommunityIcons } from "@expo/vector-icons";

const gender = [
  {
    name: "Women",
    icon: (
      <MaterialCommunityIcons name="human-female" size={50} color="black" />
    ),
  },
  {
    name: "Men",
    icon: <MaterialCommunityIcons name="human-male" size={50} color="black" />,
  },
  {
    name: "Other",
    icon: (
      <MaterialCommunityIcons name="human-non-binary" size={50} color="black" />
    ),
  },
];

export default function Questionaire2({ route }) {
  const navigation = useNavigation();
  const handlePress = (selectedGender) => {
    navigation.navigate("Questionaire3", {
      selectedAge: route.params["selectedAge"],
      selectedGender: selectedGender,
    });
  };
  return (
    <SafeAreaView style={{ flex: 1 }}>
      <ImageBackground
        source={require("../assets/sdgsImage.png")}
        style={styles.imageBackground}
        imageStyle={styles.imageStyle}
      >
        <ScrollView style={styles.scrollingViewMainContainer}>
          <View style={styles.viewContainer}>
            <View style={styles.titleViewContainer}>
              <Text style={styles.font20px600}>Which is your gender?</Text>
            </View>
            <View style={styles.shortButtonsViewContainer}>
              {gender.map((group) => (
                <View key={group.name} style={styles.shortButtonContainer}>
                  <ShortButton
                    onPress={() => handlePress(group.name)}
                    textTitle={group.name}
                    icon={group.icon}
                  />
                </View>
              ))}
            </View>
          </View>
        </ScrollView>
      </ImageBackground>
    </SafeAreaView>
  );
}
