import React from "react";
import {
  Image,
  Text,
  View,
  TouchableOpacity,
  ScrollView,
  Linking,
} from "react-native";
import { FontAwesome } from "@expo/vector-icons";
import { Entypo } from "@expo/vector-icons";
import { useNavigation } from "@react-navigation/native";
import { SimpleLineIcons } from "@expo/vector-icons";
import { styles } from "../styles/Styles";

const OrgCard = ({ org }) => {
  const navigation = useNavigation();

  const handlePress = () => {
    navigation.navigate("OrganizationDetails", org);
  };

  return (
    <ScrollView>
      <TouchableOpacity
        style={styles.orgCardContainer}
        onPress={() => handlePress(org)}
      >
        <View style={styles.orgCardStyle}>
          <Text style={styles.font18px600}>{org["Name of Organisation"]}</Text>
          <View
            style={{
              flexDirection: "row",
              justifyContent: "space-between",
              alignItems: "center",
              borderRadius: 12,
              marginTop: "3%",
              shadowColor: "#171717",
              shadowOffset: { width: -2, height: 4 },
              shadowOpacity: 0.2,
              shadowRadius: 3,
            }}
          >
            <View style={styles.contactsContainer}>
              {!org["Phone Number"] ? (
                <></>
              ) : (
                <View>
                  <View style={styles.iconAndTextContainer}>
                    <Entypo
                      name="old-mobile"
                      size={24}
                      color="#6B6B6B"
                      style={{ marginRight: 2, marginLeft: -4 }}
                    />
                    <View>
                      <Text style={styles.shortButtonTextTitle}>Phone</Text>
                    </View>
                  </View>
                  {org["Phone Number"]
                    .split("/")
                    .map((num) => num.trim())
                    .map((num) => (
                      <Text
                        style={styles.longButtonTextSubtitle}
                        key={"phone" + num}
                      >{` ${num}`}</Text>
                    ))}
                </View>
              )}
              <View style={{ marginTop: 15 }}>
                <View style={styles.iconAndTextContainer}>
                  <FontAwesome
                    name="building"
                    size={20}
                    color="#6B6B6B"
                    style={{ marginRight: 4 }}
                  />
                  <View>
                    <Text style={styles.shortButtonTextTitle}>Address</Text>
                  </View>
                </View>
                {!org["Address"] ? (
                  <></>
                ) : (
                  <Text style={styles.longButtonTextSubtitle}>
                    {org["Address"]}
                  </Text>
                )}
                <Text style={styles.longButtonTextSubtitle}>
                  {org["Code_region"]}
                </Text>
              </View>
              <View style={{ marginTop: 15 }}>
                {!org["Web Address"] ? (
                  <></>
                ) : (
                  <View>
                    <View style={styles.iconAndTextContainer}>
                      <SimpleLineIcons
                        name="globe"
                        size={20}
                        color="#6B6B6B"
                        style={{ marginRight: 6 }}
                      />
                      <View>
                        <Text style={styles.shortButtonTextTitle}>Website</Text>
                      </View>
                    </View>
                    <Text
                      style={styles.longButtonTextSubtitle}
                      onPress={() =>
                        Linking.openURL(org["Web Address"]).catch((err) =>
                          console.error("Couldn't load page", err)
                        )
                      }
                    >
                      {org?.["Web Address"]}
                    </Text>
                  </View>
                )}
              </View>
            </View>
            <View style={{ position: "relative", right: 35 }}>
              <SimpleLineIcons
                name="arrow-right"
                activeColor="black"
                size={26}
                color="black"
              />
            </View>
          </View>
        </View>
      </TouchableOpacity>
    </ScrollView>
  );
};

export default OrgCard;
