import * as React from "react";
import { TouchableOpacity, Text } from "react-native";
import { styles } from "../../styles/Styles";

export default function LongButton({ onPress, textTitle, textSubtitle }) {
  return (
    <TouchableOpacity onPress={onPress} style={styles.longButtonStyle}>
      <Text style={styles.longButtonTextTitle}>{textTitle}</Text>
      {textSubtitle && (
        <Text style={styles.longButtonTextSubtitle}>{textSubtitle}</Text>
      )}
    </TouchableOpacity>
  );
}
