import { useEffect, useState } from "react";
import * as Font from "expo-font";
import TabNavigatorBottom from "./routes/TabNavigatorBottom";
import "./i18n/i18n";
import { GestureHandlerRootView } from "react-native-gesture-handler";
import { useTheme } from "react-native-paper";

const getFonts = () =>
  Font.loadAsync({
    "montserrat-regular": require("./assets/fonts/Montserrat-Regular.ttf"),
    "montserrat-medium": require("./assets/fonts/Montserrat-Medium.ttf"),
    "montserrat-semi-bold": require("./assets/fonts/Montserrat-SemiBold.ttf"),
    "montserrat-bold": require("./assets/fonts/Montserrat-Bold.ttf"),
    "montserrat-extra-bold": require("./assets/fonts/Montserrat-ExtraBold.ttf"),
  });

export default function App() {
  const [fontsLoaded, setFontsLoaded] = useState(false);
  const theme = useTheme();

  useEffect(() => {
    getFonts().then(() => setFontsLoaded(true));
  }, []);

  if (fontsLoaded) {
    return (
      <GestureHandlerRootView
        style={{ flex: 1, maxWidth: 650 }}
        theme={(theme.colors.secondaryContainer = "#d5e8f2")}
      >
        <TabNavigatorBottom />
      </GestureHandlerRootView>
    );
  }
}
