import React from "react";
import {
  Text,
  View,
  ImageBackground,
  ScrollView,
  SafeAreaView,
} from "react-native";
import OrgCard from "./OrgCard";
import organisationsList from "../data/organisations.json";
import { styles } from "../styles/Styles";

const OrganizationsList = ({ selectedGender, selectedAge, selectedNeed }) => {
  //const mvpOrgs = organisationsList.filter((org) => org["MVP"]);
  const filteredByAgeOrgs = organisationsList.filter((org) =>
    org["Age"].includes(selectedAge)
  );

  const filteredByGenderOrgs = filteredByAgeOrgs.filter((org) =>
    org["Gender"].includes(selectedGender)
  );

  const filteredByNeedOrgs = filteredByGenderOrgs.filter((org) =>
    org["Needs"].includes(selectedNeed)
  );

  return (
    <SafeAreaView style={{ flex: 1 }}>
      <ImageBackground
        source={require("../assets/sdgsImage.png")}
        style={styles.imageBackground}
        imageStyle={styles.imageStyle}
      >
        <ScrollView style={styles.scrollingViewMainContainer}>
          <View>
            {filteredByNeedOrgs?.length === 0 ? (
              <View
                style={[
                  styles.titleViewContainer,
                  { margin: 15, marginTop: 30 },
                ]}
              >
                <Text style={styles.font16px600}>
                  No organizations found. Please select a different filter.
                </Text>
              </View>
            ) : (
              filteredByNeedOrgs?.map((org) => (
                <OrgCard org={org} key={org["Name of Organisation"]} />
              ))
            )}
          </View>
        </ScrollView>
      </ImageBackground>
    </SafeAreaView>
  );
};

export default OrganizationsList;
