import { useState } from "react";
import { TouchableOpacity, Text, View, Image, Linking } from "react-native";
import { styles } from "../../styles/Styles";
import { SimpleLineIcons } from "@expo/vector-icons";
import { Button } from "react-native-paper";

export default function SdgMoreCard({ sdgLogoAndName }) {
  const [cardFront, setCardFront] = useState(true);

  return cardFront ? (
    <TouchableOpacity
      onPress={() => setCardFront(false)}
      style={[
        styles.sdgsFrontMoreCardContainer,
        { backgroundColor: sdgLogoAndName.color },
      ]}
    >
      <View style={styles.simpleViewContainer}>
        <Image
          key={sdgLogoAndName.name}
          source={sdgLogoAndName.image}
          style={{ width: 180, height: 180 }}
          resizeMode="cover"
        />
      </View>
      <View style={styles.simpleViewContainer}>
        <SimpleLineIcons
          name="arrow-right"
          activeColor="white"
          size={26}
          color="#fff"
        />
      </View>
    </TouchableOpacity>
  ) : (
    <View
      style={[
        styles.sdgsBackMoreCardContainer,
        { backgroundColor: sdgLogoAndName.color },
      ]}
    >
      <View style={styles.simpleViewContainer}>
        <Text style={styles.sdgsCardTitleText}>{sdgLogoAndName.name}</Text>
        <Text style={styles.sdgsCardText}>{sdgLogoAndName.description}</Text>
      </View>
      <View style={styles.sdgsCardButtonContainer}>
        <Button style={styles.sdgsCardButton}>
          <Text style={styles.shortButtonTextTitle}>See all orgs</Text>
        </Button>
        <Button
          style={styles.sdgsCardButton}
          onPress={() =>
            Linking.openURL(sdgLogoAndName.url).catch((err) =>
              console.error("Couldn't load page", err)
            )
          }
        >
          <Text style={styles.shortButtonTextTitle}>See more info</Text>
        </Button>
      </View>
    </View>
  );
}
